p {
  margin: 0;
}

.app-box {
  height: 100%;
  background-color: whitesmoke;
  display: grid;
  grid-template:
            "a a a" 150px
            "b c c" 1fr
            "b c c" / 1fr 1fr 1fr;
}

.app-box>* {
  border: 1px solid black;
}

.app-box>h1 {
  box-sizing: border-box;
  grid-area: a;
  margin: 0;
  padding-top: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: auto;
  width: 33.33%;
  width: calc(1px + (100% / 3));
  border-right: 2px solid black;
  border-bottom: 0px solid whitesmoke;
}

.app-box>div:first-of-type {
  grid-area: b;
  box-sizing: border-box;
  border-top: 2px solid black;
}

.app-box>div:last-child {
  grid-area: c;
  border-top: 2px solid black;
}

.app-box>div {
  display: grid;
  grid-template:
            "a" 150px
            "b" 1fr;
}

