  padding-left: 5px;
  padding-right: 5px;
}

[type="checkbox"] {
  opacity: 0;
}

[type="checkbox"] + label {
  position: relative;
  cursor: pointer;
  display: inline-block;
  color: black;
  padding-left: 25px;
}

[type="checkbox"] + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 18px;
  width: 18px;
  outline: 2px solid darkgray;
  background: whitesmoke;
}

[type="checkbox"]:checked + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 18px;
  width: 18px;
  outline: 2px solid rgba(0,128,0,255);
  background: whitesmoke;
}

[type="checkbox"]:checked+ label::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 18px;
  width: 18px;
  outline: 2px solid rgba(0,128,0,255);
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
  transform: scale(1);
  opacity: 1;
  transition: all .3s ease;
}

[type="checkbox"]:not(:checked)+ label::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 18px;
  width: 18px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
  transform: scale(0);
  opacity: 0;
}

