  padding-right: 0;
  width: 100%;
}

#priority-list-ul>li>label {
  flex-grow: 1;
}

#priority-list-ul>li>input {
  flex-shrink: 0;
}

#priority-list-ul>li::marker {
  flex-shrink: 0;
}

li {
  word-wrap: break-word;
  padding-right: 20px;
}

